.btnStyle{
    padding:0;
    border: 1px solid yellow;
    padding: 4px 10px 4px 10px;
    border-radius: 4px;
    color:white;
}


.btnStyle {
    border-radius: 4px;
    background-color: #151227;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 24px;
    padding: 2px 10px 2px 10px;
    width: 150px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
  }
  
  .btnStyle span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .btnStyle span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .btnStyle:hover span {
    padding-right: 25px;
  }
  
  .btnStyle:hover span:after {
    opacity: 1;
    right: 0;
  }